@import url('https://fonts.googleapis.com/css?family=Ubuntu|Lora');
.NavBar{
	color: black;
	width: 100vw;
	display: flex;
	height: 9vh;
	padding: 2vh;
/*	border-bottom: 0.5px solid lightgray;*/
	/* background-color: #89a9d2; */


}

.overlay {
  height: 100%;
  width: 0%;
  position: fixed;
  z-index: 1;
  top: 0;
  right: 0;
  background-color:#adadad;
/*  background-color: rgba(0,0,0, 0.9);*/
  overflow-y: hidden;
  transition: 0.5s;
} 
.overlayAdOne {
	height: 8vh;
	width: 9cm;
	position: fixed;
	z-index: 1;
	top: 0;
	left: 35vw;
	/* background-color:whitesmoke; */
	border-radius: 6px;
	margin-top: 1vh;
  /*  background-color: rgba(0,0,0, 0.9);*/
  border: 0.5px solid #28282b;
	overflow-y: hidden;
	/* transition: 0.5s; */
  }
  .overlay-contentAdOne{
	display: flex;
	column-gap: .1cm;
	padding-left: .1cm;
	/* justify-content: center; */
	align-items: center;
	align-content: center;
	padding-top: .09cm;

  }
  .logoAdOne{
	height: 7vh;
	width: 1.5cm;
	/* border: .5px solid black; */
	font-size: 13px;
	text-align: center;
  }
  .logoAdImgOne{
	height: 6.5vh;
	width: 1.3cm;
  }
  .adOneContent{
		display: flex;
		flex-direction: column;
  }
  .adBrandNameOne{
	height: 3vh;
	width: 7.4cm;
	/* border: 1px solid black; */
	display: flex;
	font-weight: 600;
	column-gap: 10px;
  }
  .adBrandTagLineOne{
	height: 3.5vh;
	width: 7.4cm;
	/* border: 1px solid black; */
	font-size: 14px;
	font-weight: 600;
  }
  .bNameOneAd{
	margin: 0px 0px;
	width:6.8cm ;
	/* border: 1px solid black; */
  }
  .adTagOne{
	font-weight: bold;
	font-size: 12px;
	width: 0.6cm;
	/* color: yellow; */
	height: 2vh;
	/* border: 1px solid black; */
	background-color: lightgray;
	border-radius: 5px;
	text-align: center;
	/* margin-top: 0px; */
  }
  
.logoPic{
		width: 40px;
		height: 45px;
		margin-top: 7px;
		
}

.overlay-content {
  position: relative;
  top: 15%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: black;
  display: block;
  transition: 0.3s;
}

.overlay a:hover, .overlay a:focus {
  color: #f1f1f1;
}

.overlay .closebtn {
  position: absolute;
  top: 10px;
  right: 45px;
  font-size: 60px;
}

.m{
  height: 3vh;
  display: none;
  cursor: pointer;
}
.menu{
	display: flex;
	width: 22vw;
	column-gap: 1.3vw;
	align-items: center;
	justify-content: center;
}
.logo{
	width: 70vw;
	display: flex;
	justify-content: flex-start;
	margin-left: 5vw;
	cursor: pointer;
	column-gap: 0.5vw;
/*	margin-top: -0.5vw;*/
	align-items: center;

}

.logoT{
	color: black;
	font-size: 25px;
	font-family: Ubuntu;


}
.menuItem{
	font-size: 20px;
	font-family: Ubuntu;
	cursor: pointer; 
	color: black;

}

.menuItem:hover{
	text-decoration: underline;
}

.coffee{
	height: 30px;
}
@media screen and (max-width: 900px){
	.m{
		display: inline;

	}
	.menuItem{
		display: none;
	}
	.menu{
		justify-content: flex-end;
		width: 20vw;
		padding-right: 2vw;
		column-gap: 4vw;
	}
	.logo{
		width: 65vw;
	}

	.overlay a{
      font-size: 30px;
    }
    .coffee{
    	height: 30px;
    }
	
}
@media screen and (max-width:850px){
	.overlayAdOne{
		display: none;
	}
}

@media screen and (max-width: 500px){
  
    .overlay a{
      font-size: 20px;
    }
    .logoT{
    	display: none;
    }
}
@media screen and (max-width: 350px){
	
	.coffee{
    	height: 30px;
    }
}
@media screen and (max-width: 300px){
	
	.coffee{
    	height: 25px;
    }
}



